import React, { Component } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { Switch, Route } from "react-router-dom";

import { sidebarTrigger, isAuthorized } from "../../actions/actionCreator";

import Sidebar from "../../components/Sidebar/Sidebar";
import Header from "../../components/Header/Header";
import Settings from "../../components/Settings/Settings";
import MainPage from "../MainPage/MainPage";
import Login from "../Login/Login";
import AddNote from "../AddNote/AddNote";

import "./Todo.scss";

class Todo extends Component {
  render() {
    const { sidebarTrigger, sidebar, isAuthorized, authorization } = this.props;
    return (
      <>
        <div
          className={
            sidebar
              ? "page page__sidebar_animation page__sidebar_on"
              : "page page__sidebar_animation"
          }
        >
          <Header
            sidebarTrigger={sidebarTrigger}
            sidebar={sidebar}
            isAuthorized={isAuthorized}
            authorization={authorization}
          />
          <div className="page__inner">
            <main className="main">
              <Switch>
                <Route exact path="/main" component={MainPage} />
                <Route path="/login" component={Login} />
                <Route path="/add-note" component={AddNote} />
                <Route exact path="/settings" component={Settings} />
              </Switch>
            </main>
          </div>
        </div>
        <Sidebar sidebar={sidebar} />
      </>
    );
  }
}

Todo.propTypes = {
  sidebarTrigger: PropTypes.func,
  sidebar: PropTypes.bool,
  authorization: PropTypes.bool,
  isAuthorized: PropTypes.func
};

Todo.defaultProps = {
  sidebarTrigger: () => {},
  sidebar: true,
  authorization: true,
  isAuthorized: () => {}
};

export default connect(
  ({ sidebar, authorization }) => ({ sidebar, authorization }),
  { sidebarTrigger, isAuthorized }
)(Todo);
