import React, { Component } from "react";
import PropTypes from "prop-types";
import Cookies from "js-cookie";
import { MdMoreVert, MdAccessTime } from "react-icons/md";
import { Form, Row, Col, Button } from "react-bootstrap";

import "./NotesListItem.scss";

class NotesListItem extends Component {
  state = {
    isEdit: false,
    title: "",
    text: "",
    priority: "low"
  };

  handleFlagChange = () => {
    this.setState({
      isEdit: !this.state.isEdit
    });
  };

  handleChange = ({ target: { value, name } }) => {
    this.setState({
      [name]: value
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    const { title, text, priority, isEdit } = this.state;

    if (
      title.length > 1 ||
      text.length > 1 ||
      priority !== this.props.priority
    ) {
      const { id, editNote } = this.props;

      editNote(id, title, text, priority);
    }
    return this.setState({
      isEdit: !isEdit
    });
  };

  render() {
    const {
      id,
      text,
      isCompleted,
      title,
      priority,
      date,
      removeNote,
      completeNote
    } = this.props;
    const { isEdit } = this.state;
    return (
      <div className="notes__inner" id={id}>
        {isEdit ? (
          <Form className="col-md-8" onSubmit={this.handleSubmit}>
            <Form.Group as={Row} controlId="formHorizontalEmail">
              <Form.Label column sm={2}>
                Title
              </Form.Label>
              <Col sm={10}>
                <Form.Control
                  type="text"
                  placeholder="Title"
                  name="title"
                  onChange={this.handleChange}
                  autoFocus
                />
              </Col>
            </Form.Group>

            <Form.Group as={Row} controlId="formHorizontalPassword">
              <Form.Label column sm={2}>
                Text
              </Form.Label>
              <Col sm={10}>
                <Form.Control
                  as="textarea"
                  rows="3"
                  placeholder="Text"
                  name="text"
                  onChange={this.handleChange}
                />
              </Col>
            </Form.Group>
            <fieldset>
              <Form.Group as={Row}>
                <Form.Label as="legend" column sm={2}>
                  Priority
                </Form.Label>
                <Col sm={10}>
                  <Form.Control
                    as="select"
                    defaultValue={priority}
                    name="priority"
                    onChange={this.handleChange}
                  >
                    <option value="low">Low Priority</option>
                    <option value="medium">Medium Priority</option>
                    <option value="hight">Hight Priority</option>
                  </Form.Control>
                </Col>
              </Form.Group>
            </fieldset>
            <Form.Group as={Row}>
              <Col sm={{ span: 10, offset: 2 }}>
                <Button type="submit">Save</Button>
              </Col>
            </Form.Group>
          </Form>
        ) : (
          <>
            {isCompleted ? (
              <div className="notes__item_top notes__item_completed">
                Completed
              </div>
            ) : (
              <div className="notes__item_top">
                <span className="notes__item_priority">
                  {priority === "low"
                    ? "Low Priority"
                    : priority === "medium"
                    ? "Medium Priority"
                    : "Hight Priority"}
                </span>
                <div className="notes__item_date-wrapper">
                  <span className="notes__item_date-icon">
                    <MdAccessTime />
                  </span>
                  <span className="notes__item_date">{date}</span>
                </div>
              </div>
            )}
            <div className="notes__item_content">
              <div
                className={
                  "notes__item_priority-color " +
                  (priority === "low"
                    ? "notes__item_priority-low"
                    : priority === "medium"
                    ? "notes__item_priority-medium"
                    : "notes__item_priority-hight")
                }
              ></div>
              <div className="notes__item_content-inner">
                <div className="notes__item_content-title">{title}</div>
                <div className="notes__item_content-text">{text}</div>
              </div>
              {Cookies.get("sid") ? (
                <div className="notes__dropdown notes__right_item">
                  <div className="notes__dropdown_icon">
                    <MdMoreVert />
                  </div>
                  <div className="notes__dropdown_list">
                    {isCompleted ? null : (
                      <button
                        className="notes__dropdown_item"
                        onClick={this.handleFlagChange}
                      >
                        Edit
                      </button>
                    )}
                    <button
                      className="notes__dropdown_item"
                      onClick={() => removeNote(id)}
                    >
                      Remove
                    </button>
                    <button
                      className="notes__dropdown_item"
                      onClick={() => completeNote(id, !isCompleted)}
                    >
                      {isCompleted ? "Not Completed" : "Completed"}
                    </button>
                  </div>
                </div>
              ) : null}
            </div>
          </>
        )}
      </div>
    );
  }
}

NotesListItem.propTypes = {
  notes: PropTypes.array,
  removeNote: PropTypes.func,
  completeNote: PropTypes.func,
  editNote: PropTypes.func
};

NotesListItem.defaultProps = {
  notes: [],
  removeNote: () => {},
  completeNote: () => {},
  editNote: () => {}
};

export default NotesListItem;
