import React from "react";
import PropTypes from "prop-types";
import Cookies from "js-cookie";
import { IoIosMenu } from "react-icons/io";
import { MdSortByAlpha, MdFilterList } from "react-icons/md";
import { Link } from "react-router-dom";

import UserMenu from "../../containers/UserMenu/UserMenu";

import "./Header.scss";

const Header = ({ sidebar, sidebarTrigger, isAuthorized, authorization }) => (
  <>
    <header className="header">
      <div className="page__inner">
        <div className="header__wrapper">
          <button
            className="header__button"
            onClick={() => sidebarTrigger(!sidebar)}
          >
            <IoIosMenu />
          </button>
          <div className="header__right">
            <button className="header__right_item header__right_button">
              <MdFilterList />
            </button>
            <button className="header__right_item header__right_button">
              <MdSortByAlpha />
            </button>
            {Cookies.get("sid") ? (
              <Link
                className="header__right_item header__right_link"
                to="/add-note"
              >
                + Add ToDo
              </Link>
            ) : null}
            {Cookies.get("sid") ? (
              <UserMenu
                authorization={authorization}
                isAuthorized={isAuthorized}
              />
            ) : (
              <Link
                className="header__right_item header__right_link"
                to="/login"
              >
                Sign In
              </Link>
            )}
          </div>
        </div>
      </div>
    </header>
  </>
);

Header.propTypes = {
  sidebar: PropTypes.bool,
  authorization: PropTypes.bool,
  sidebarTrigger: PropTypes.func,
  isAuthorized: PropTypes.func
};

Header.defaultProps = {
  sidebar: true,
  authorization: false,
  sidebarTrigger: () => {},
  isAuthorized: () => {}
};

export default Header;
