import {
  SIDEBAR_TRIGGER,
  IS_AUTHORIZED,
  ADD_NOTE,
  REMOVE_NOTE,
  COMPLETE_NOTE,
  EDIT_NOTE
} from "../constants";

export const sidebarTrigger = trigger => ({
  type: SIDEBAR_TRIGGER,
  trigger
});
export const isAuthorized = trigger => ({
  type: IS_AUTHORIZED,
  trigger
});
export const addNote = (id, text, isCompleted, title, priority, date) => ({
  type: ADD_NOTE,
  id,
  text,
  isCompleted,
  title,
  priority,
  date
});
export const removeNote = id => ({
  type: REMOVE_NOTE,
  id
});
export const completeNote = (id, value) => ({
  type: COMPLETE_NOTE,
  id,
  value
});

export const editNote = (id, title, text, priority) => ({
  type: EDIT_NOTE,
  id,
  title,
  text,
  priority
});
