import { ADD_NOTE, REMOVE_NOTE, COMPLETE_NOTE, EDIT_NOTE } from "../constants";
import { load } from "redux-localstorage-simple";

let NOTES = load({ namespace: "todo" });

if (!NOTES || !NOTES.notes || !NOTES.notes.length) {
  NOTES = {
    notes: []
  };
}

const notes = (
  state = NOTES.notes,
  { type, id, text, isCompleted, title, priority, date, value }
) => {
  switch (type) {
    case ADD_NOTE:
      return [
        ...state,
        {
          id,
          text,
          isCompleted,
          title,
          priority,
          date
        }
      ];
    case REMOVE_NOTE:
      return [...state].filter(note => note.id !== id);
    case COMPLETE_NOTE:
      return [...state].map(note => {
        if (note.id === id) {
          note.isCompleted = value;
        }
        return note;
      });
    case EDIT_NOTE:
      return [...state].map(note => {
        if (note.id === id) {
          note.title = title;
          note.text = text;
          note.priority = priority;
        }
        return note;
      });
    default:
      return state;
  }
};

export default notes;
