import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Form, Row, Col, Button } from "react-bootstrap";

import { addNote } from "../../actions/actionCreator";

import "./AddNote.scss";

class AddNote extends Component {
  state = {
    title: "",
    text: "",
    priority: "low"
  };

  handleChange = ({ target: { value, name } }) => {
    this.setState({
      [name]: value
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    const { title, text, priority } = this.state;

    if (title.length > 5 && text.length > 5) {
      const { addNote } = this.props;
      const date = new Date().toLocaleString();

      addNote(new Date().getTime(), text, false, title, priority, date);

      this.props.history.push("/main");
    }
  };

  render() {
    const { priority } = this.state;
    return (
      <div className="add-note__wrapper">
        <Form className="col-md-8" onSubmit={this.handleSubmit}>
          <Form.Group as={Row} controlId="formHorizontalEmail">
            <Form.Label column sm={2}>
              Title
            </Form.Label>
            <Col sm={10}>
              <Form.Control
                type="text"
                placeholder="Title"
                name="title"
                onChange={this.handleChange}
              />
            </Col>
          </Form.Group>

          <Form.Group as={Row} controlId="formHorizontalPassword">
            <Form.Label column sm={2}>
              Text
            </Form.Label>
            <Col sm={10}>
              <Form.Control
                as="textarea"
                rows="3"
                placeholder="Text"
                name="text"
                onChange={this.handleChange}
              />
            </Col>
          </Form.Group>
          <fieldset>
            <Form.Group as={Row}>
              <Form.Label as="legend" column sm={2}>
                Priority
              </Form.Label>
              <Col sm={10}>
                <Form.Control
                  as="select"
                  value={priority}
                  name="priority"
                  onChange={this.handleChange}
                >
                  <option value="low">Low Priority</option>
                  <option value="medium">Medium Priority</option>
                  <option value="hight">Hight Priority</option>
                </Form.Control>
              </Col>
            </Form.Group>
          </fieldset>
          <Form.Group as={Row}>
            <Col sm={{ span: 10, offset: 2 }}>
              <Button type="submit">Add ToDo</Button>
            </Col>
          </Form.Group>
        </Form>
      </div>
    );
  }
}

AddNote.propTypes = {
  addNote: PropTypes.func
};

AddNote.defaultProps = {
  addNote: () => {}
};

export default connect(
  () => ({}),
  { addNote }
)(AddNote);
