import React, { Component } from "react";
import { connect } from "react-redux";
import Cookies from "js-cookie";
import PropTypes from "prop-types";
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn } from "mdbreact";

import { isAuthorized } from "../../actions/actionCreator";

import "./Login.scss";

import { USER, COOKIE_EXPIRES } from "../../constants";

class Login extends Component {
  state = {
    email: "",
    password: "",
    message: ""
  };
  handleChange = ({ target: { value, name } }) => {
    this.setState({
      [name]: value
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    const { isAuthorized, authorization } = this.props;
    Cookies.get("sid")
      ? this.setState({ message: "You're already logged" })
      : USER.map(({ id, email, password, image }) => {
          if (email === this.state.email) {
            if (password === this.state.password) {
              Cookies.set(
                "sid",
                { id, email, image },
                { expires: COOKIE_EXPIRES }
              );
              isAuthorized(!authorization);
              this.props.history.push("/main");
            } else {
              this.setState({
                message: "Login or password is incorrect"
              });
            }
          } else {
            this.setState({
              message: "Login or password is incorrect"
            });
          }
          return 0;
        });
  };
  render() {
    const { message } = this.state;
    return (
      <div className="login__container">
        <MDBContainer>
          <MDBRow className="justify-content-center">
            <MDBCol md="6">
              <span className="login__message">{message}</span>
              <form onSubmit={this.handleSubmit}>
                <p className="h5 text-center mb-4">Sign in</p>
                <div className="grey-text">
                  <MDBInput
                    name="email"
                    onChange={this.handleChange}
                    label="Type your email"
                    type="email"
                  />
                  <MDBInput
                    name="password"
                    onChange={this.handleChange}
                    label="Type your password"
                    type="password"
                  />
                </div>
                <div className="text-center">
                  <MDBBtn type="submit">Login</MDBBtn>
                </div>
              </form>
            </MDBCol>
          </MDBRow>
        </MDBContainer>
      </div>
    );
  }
}

Login.propTypes = {
  isAuthorized: PropTypes.func,
  authorization: PropTypes.bool
};
Login.defaultProps = {
  isAuthorized: () => {},
  authorization: false
};

export default connect(
  ({ authorization }) => ({ authorization }),
  { isAuthorized }
)(Login);
