import React from "react";
import PropTypes from "prop-types";
import { MdViewList, MdViewModule } from "react-icons/md";

import NotesListItem from "../NotesListItem/NotesListItem";

import "./NotesList.scss";

const NotesList = ({ notes, removeNote, completeNote, editNote }) => (
  <div className="todo__list">
    <div className="todo__bar">
      <div className="todo__bar_title">To Do</div>
      <div className="todo__bar_layout">
        <button className="todo__bar_list">
          <MdViewList />
        </button>
        <button className="todo__bar_module">
          <MdViewModule />
        </button>
      </div>
    </div>
    <div className="notes__wrapper">
      {notes.map(({ id, text, isCompleted, title, priority, date }) =>
        isCompleted ? null : (
          <NotesListItem
            id={id}
            key={id}
            text={text}
            title={title}
            priority={priority}
            isCompleted={isCompleted}
            date={date}
            removeNote={removeNote}
            completeNote={completeNote}
            editNote={editNote}
          />
        )
      )}
    </div>

    <div className="todo__bar todo__bar_completed">
      <div className="todo__bar_title">Completed</div>
    </div>
    <div className="notes__wrapper">
      {notes.map(({ id, text, isCompleted, title, priority, date }) =>
        isCompleted ? (
          <NotesListItem
            id={id}
            key={id}
            text={text}
            title={title}
            priority={priority}
            isCompleted={isCompleted}
            date={date}
            removeNote={removeNote}
            completeNote={completeNote}
          />
        ) : null
      )}
    </div>
  </div>
);

NotesList.propTypes = {
  notes: PropTypes.array,
  removeNote: PropTypes.func,
  completeNote: PropTypes.func,
  editNote: PropTypes.func
};

NotesList.defaultProps = {
  notes: [],
  removeNote: () => {},
  completeNote: () => {},
  editNote: () => {}
};

export default NotesList;
