import React, { Component } from "react";
import Cookies from "js-cookie";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";

import "./UserMenu.scss";

class UserMenu extends Component {
  handleLogOut = () => {
    Cookies.remove("sid");
  };
  handleAuthorization = () => {
    const { authorization, isAuthorized } = this.props;
    isAuthorized(!authorization);
  };
  render() {
    return (
      <div className="header__dropdown header__right_item">
        <img
          src={JSON.parse(Cookies.get("sid")).image}
          className="header__dropdown_image"
          alt="User"
        />
        <div className="header__dropdown_list">
          <Link
            className="header__dropdown_item"
            to="/main"
            onClick={() => {
              this.handleLogOut();
              this.handleAuthorization();
            }}
          >
            Log Out
          </Link>
        </div>
      </div>
    );
  }
}

UserMenu.propTypes = {
  authorization: PropTypes.bool,
  isAuthorized: PropTypes.func
};

UserMenu.defaultProps = {
  authorization: true,
  isAuthorized: () => {}
};

export default UserMenu;
